import React from 'react';
import { Chart as LucideChart } from 'lucide-react';

interface ButtonProps {
  data: string;
  color: string;
  icon?: React.ComponentType;
}

const RoundedButton = ({ data, color, icon: IconComponent }: ButtonProps) => {
  return (
    <div className={`bg-${color} rounded-xl flex items-center justify-center px-4 py-2 w-24`}>
      {IconComponent && <IconComponent size={20} className="mr-2" />} {/* Render icon if provided */}
      <p>{data}</p>
    </div>
  );
};

export default RoundedButton;