import RoundedButton from '@/components/RoundedButton';
import { Chart as LucideChart } from 'lucide-react';

const ExamplePage = () => {
    return (
      <div className="flex flex-col items-center justify-center h-screen space-y-4">
        <RoundedButton data="Chart" color="blue-500" icon={LucideChart} />
        <RoundedButton data="Save" color="green-500" /> {/* Button without an icon */}
        <RoundedButton data="Cancel" color="red-500" />
        <RoundedButton data="Edit" color="yellow-500" icon={LucideChart}/>
      </div>
    );
  };
  
  export { ExamplePage }; // Export the page